<template>
  <div class="course-screen-question-view">
    <head-top title="截屏提问">
      <span slot="right" v-if="screenQuestion.state === 1" @click="selectAnswerShow = true">
        <van-icon class-prefix="iconfont icon-jieshu" color="#fff" class="head-right"></van-icon>
      </span>
    </head-top>

    <div class="sub_nav">
      <span>{{ screenQuestion.index }}、{{typeNames[screenQuestion.type - 1]}}</span>
      <van-tag plain type="success">{{screenQuestion.ModeName}}</van-tag>
      <span>{{screenQuestion.stateName}}</span>
    </div>
    <div class="sub_module">
      <r-image width="90vw" height="30vw" :src="screenQuestion.imgSrc" class="item-left">
      </r-image>
    </div>
    <div class="sub_module">
      <div class="flex-between">
        <span>时长：{{screenQuestion.time}}</span>
        <span>分数：{{screenQuestion.score}}</span>
        <span>下发：{{screenQuestion.createTime}}</span>
      </div>
    </div>

    <template v-if="screenQuestion.questionMode === randomPick">
      <div class="sub_nav">
        <span>随机挑人</span>
      </div>
      <div class="sub_module">
        <span v-if="answerPersons.length===0">暂无数据</span>
        <div class="flex-wrap-between" v-else>
          <div v-for="(sub, k) in answerPersons" :key="k" class="student_card van-hairline--surround">
            <img :src="sub.imgSrc" class="col_4" alt=""/>
            <div>{{sub.studentName}}</div>
            <van-tag plain type="primary">{{sub.answer}}</van-tag>
          </div>
          <div class="student_card" style="width: 19vw;" v-for="d in Math.abs(4 - answerPersons.length % 4) % 4"
               :key="-d">
          </div>
        </div>
      </div>
    </template>

    <template v-else>
      <div class="sub_nav">
        <span>提交成员</span>
        <span>{{submitInfo.subNum}} / {{submitInfo.total}}</span>
      </div>
      <div class="sub_module">
        <span v-if="submitInfo.persons.length===0">暂无数据</span>
        <div class="flex-wrap-between" v-else>
          <div v-for="(sub, k) in submitInfo.persons" :key="k" class="student_card van-hairline--surround"
               @click="toAnswer(sub)">
            <img :src="sub.imgSrc" class="col_4" alt=""/>
            <div>{{sub.studentName}}</div>
          </div>
          <div class="student_card" style="width: 19vw;" v-for="d in Math.abs(4 - submitInfo.persons.length % 4) % 4"
               :key="-d">
          </div>
        </div>
      </div>
    </template>

    <template v-if="screenQuestion.state===2">
      <div class="sub_nav">
        <span>正确答案</span>
      </div>
      <div class="sub_module">
        <div>{{screenQuestion.answer}}</div>
        <div class="flex-between">
          <div class="van-hairline--surround list-col">
          <span>正确率：
            <span :style="{color:'#07c160'}">{{screenQuestion.trueRate || '0%'}}</span>
          </span>
          </div>
          <div class="van-hairline--surround list-col">
          <span>错误率：
                  <span :style="{color:'#ee0a24'}">{{screenQuestion.falseRate || '0%'}}</span>
          </span>
          </div>
        </div>
      </div>
    </template>

    <template v-if="screenQuestion.type + '' !== '4'">
      <div class="sub_nav">
        <span>作答情况</span>
      </div>
      <div class="sub_module">
        <div class="flex-wrap-between">
          <div v-for="(item, index) in optionPersons" :key="index" class="van-hairline--surround list-col"
               @click="showOptionPersons(item)">
            <span>{{item.name}}：{{item.total}}</span>
          </div>
          <div v-for="k in (4 - optionPersons.length % 4) % 4" :key="-k" class=" list-col">
          </div>
        </div>
      </div>
    </template>

    <template v-if="screenQuestion.state !== 1 && screenQuestion.questionMode === allAnswer">
      <div class="sub_nav">
        <span>调研情况</span>
        <van-button v-if="screenQuestion.survey !== 1 && screenQuestion.state===2" type="info" size="small"
                    @click="toSurvey">
          下发调研
        </van-button>
        <span v-else-if="screenQuestion.survey === 1">已下发</span>
      </div>
      <div class="sub_module" v-if="screenQuestion.survey === 1">
        <div class="flex-between" v-if="screenQuestion.state !== 1">
          <div class="van-hairline--surround list-col">
            <span>已掌握：{{surveyInfo.yes}}</span>
          </div>
          <div class="van-hairline--surround list-col">
            <span>未掌握：{{surveyInfo.no}}</span>
          </div>
        </div>
        <r-lack-data v-else></r-lack-data>
      </div>
    </template>

    <van-popup v-model="selectAnswerShow" position="bottom">
      <van-cell>
        <span slot="icon" @click="selectAnswerShow = false" style="color: #1989fa;">取消</span>
        <span slot="title">
          {{screenQuestionType=== '4' ? '请填写答案' : '请选择正确答案'}}
        </span>
        <span slot="right-icon" @click="onConfirm" style="color: #1989fa;">确定</span>
      </van-cell>

      <van-radio-group v-if="screenQuestionType === '1' " v-model="answer">
        <van-cell-group>
          <van-cell v-for="item in answers" :key="item" :title="item" clickable @click="answer = item">
            <van-radio slot="right-icon" :name="item"></van-radio>
          </van-cell>
        </van-cell-group>
      </van-radio-group>

      <van-checkbox-group v-else-if="screenQuestionType === '2' " v-model="multiAnswer">
        <van-cell-group>
          <van-cell v-for="item in answers" :key="item" :title="item" clickable @click="multiChecked(item)">
            <van-checkbox slot="right-icon" sharp="square" :name="item"></van-checkbox>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>

      <van-radio-group v-else-if=" screenQuestionType === '3' " v-model="answer">
        <van-cell-group>
          <van-cell v-for="item in judgeValues" :key="item" :title="item" clickable @click="answer = item">
            <van-radio slot="right-icon" :name="item"></van-radio>
          </van-cell>
        </van-cell-group>
      </van-radio-group>

      <van-field v-else v-model="answer" name="答案" label="" placeholder="请填写答案" type="textarea" maxlength="100"
                 autofocus autosize show-word-limit :rules="[{ required: true, message: '请填写答案' }]">
      </van-field>
    </van-popup>

    <!-- 选项人员列表-->
    <van-popup v-model="optionPersonsShow" position="bottom" closeable>
      <div class="sub_nav" style="line-height: 48px;" v-if="optionPersonsSelected">
        <span>以下人员，提交选项为{{optionPersonsSelected.name}}</span>
      </div>
      <div class="sub_module" v-if="optionPersonsSelected">
        <span v-if="optionPersonsSelected.persons.length===0">暂无数据</span>
        <div class="flex-wrap-between" v-else>
          <div v-for="(sub, k) in optionPersonsSelected.persons" :key="k" class="student_card van-hairline--surround">
            <img :src="sub.imgSrc" class="col_4" alt=""/>
            <div>{{sub.studentName}}</div>
          </div>
          <div class="student_card" style="width: 19vw;"
               v-for="d in Math.abs(4 - optionPersonsSelected.persons.length % 4) % 4"
               :key="-d">
          </div>
        </div>
      </div>
    </van-popup>

    <!-- 人员答题显示-->
    <van-popup v-model="answerPersonsShow" position="bottom" closeable>
      <div class="sub_nav" style="line-height: 48px;">
        <span v-if="screenQuestion.type !== 4">作答明细</span>
        <span v-else>答题评分</span>
      </div>
      <div v-if="answerPersonsSelected">
        <van-field v-model="answerPersonsSelected.studentName" label="提交学生：" disabled></van-field>
        <van-field v-model="screenQuestion.answer" label="正确答案：" disabled></van-field>
        <van-field label="提交答案：" disabled>
          <template #input>
            <span v-if="screenQuestion.type !== 4" :style="{color: '#969799'}">{{answerPersonsSelected.answer}}</span>
            <div v-else class="answer-content" v-html="transform(answerPersonsSelected.answer)"></div>
          </template>
        </van-field>
        <van-field v-model="screenQuestion.score" label="本题分数：" disabled></van-field>
        <van-field name="stepper" label="作答得分：">
          <template #input>
            <span v-if="screenQuestion.type !== 4"
                  :style="{color: '#969799'}">{{answerPersonsSelected.score || 0}}</span>
            <van-stepper v-else v-model="answerPersonsSelected.score" integer min="0"
                         :max="screenQuestion.score"></van-stepper>
            <span style="padding-left: 10px;">分</span>
          </template>
        </van-field>
        <!--        <van-field v-model="answerPersonsSelected.comment" label="答案点评" placeholder="请填写答案点评" type="textarea" maxlength="100"-->
        <!--                   autosize show-word-limit>-->
        <!--        </van-field>-->
        <van-button v-if="screenQuestion.type === 4" type="primary" @click="toScore" style="width: 100%;">提交
        </van-button>
      </div>
      <r-lack-data v-else></r-lack-data>
    </van-popup>
  </div>
</template>

<script>
import RImage from "@/components/r-image";
import defaultImg from "../../../../assets/boy_img.png";
// import axios from "axios";
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";
import { transformValue } from "@/utils/index";
import { ModulePicture, ScreenPattern, screenPatternName, ScreenSubjectStateName } from "@/utils/enum";

export default {
  name: "screenQuestionView",
  components: { RImage },
  data () {
    return {
      screenQuestion: this.$store.getters.screenQuestion,
      typeNames: ['单选题', '多选题', '判断题', '问答题'],
      submitInfo: {
        total: 0,
        subNum: 0,
        persons: []
      },
      judgeValues: ['对', '错'],
      options: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J'],
      optionPersons: [],
      optionPersonsSelected: null,
      optionPersonsShow: false,

      answerPersons: [],
      answerPersonsShow: false,
      answerPersonsSelected: null,

      allAnswer: ScreenPattern.all,
      randomPick: ScreenPattern.random,
      surveyInfo: {
        yes: 0,
        no: 0
      },

      answer: "",
      multiAnswer: [],
      realAnswer: "",
      answerPictureType: ModulePicture.ScreenAnswer,
      selectAnswerShow: false
    };
  },
  computed: {
    screenQuestionType () {
      return this.screenQuestion ? this.screenQuestion.type + "" : "1";
    },
    answers () {
      if (['1', '2'].indexOf(this.screenQuestionType) > -1) {
        return this.options.slice(0, this.screenQuestion.optionsNum);
      } else if (this.screenQuestionType === '3') {
        return ['对', '错'];
      } else {
        return [];
      }
    }

  },
  mounted () {
    // this.init();
    this.getDataInfo();
    this.getSubmitPerson();
    if (this.screenQuestion.questionMode === this.randomPick) this.getAnswerPerson();
    if (this.screenQuestion.survey === 1) this.getSurveyPerson();
  },
  methods: {
    transform (value) {
      return transformValue("show", value, this.$gatewayHttp.resourse.picture);
    },
    multiChecked (item) {
      const index = this.multiAnswer.findIndex(r => r === item);
      if (index > -1) {
        this.multiAnswer.splice(index, 1);
      } else {
        this.multiAnswer.push(item);
      }
    },
    getDataInfo () {
      this.optionPersons = [];
      this.$gatewayHttp.screenQuestion.getDataInfo({
        ssID: this.screenQuestion.id
      }).then(res => {
        if (res.Result) {
          this.screenQuestion = {
            id: res.Data["SSID"],
            type: res.Data["SSType"],
            imgSrc: this.$gatewayHttp.resourse.picture(res.Data["SSContent"], ModulePicture.ScreenSubject),
            state: res.Data["State"],
            stateName: ScreenSubjectStateName(res.Data["State"]),
            time: res.Data["SSTime"],
            score: res.Data["SSScore"],
            optionsNum: res.Data["SSOption"],
            questionMode: res.Data["SSPattern"],
            ModeName: screenPatternName(res.Data["SSPattern"]),
            index: res.Data["SSSerial"],
            answer: res.Data["SSAnswer"],
            survey: res.Data["SSSurvey"],
            createTime: formatDate(res.Data["CreateTime"], "hh:mm:ss"),
            trueRate: res["CorrectRate"],
            falseRate: res["ErrorRate"]
          };
          if (res.Options && res.Options.length > 0) {
            res.Options.forEach(item => {
              this.optionPersons.push({
                name: item["OptionName"],
                total: item["Total"],
                persons: []
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getSubmitPerson () {
      this.submitInfo = {
        total: 0,
        subNum: 0,
        persons: []
      };
      this.$gatewayHttp.screenQuestion.getSubmitPerson({
        ssID: this.screenQuestion.id
      }).then(res => {
        if (res.Result) {
          this.submitInfo.subNum = res["SubNum"] || 0;
          this.submitInfo.total = res["Total"] || 0;
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.submitInfo.persons.push({
                studentID: item["StudentID"],
                studentName: item["StudentName"],
                imgSrc: defaultImg
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getOptionPerson () {
      this.optionPersonsSelected.persons = [];
      this.$gatewayHttp.screenQuestion.getOptionPerson({
        ssID: this.screenQuestion.id,
        answer: this.optionPersonsSelected.name
      }).then(res => {
        if (res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.optionPersonsSelected.persons.push({
              studentID: item["StudentID"],
              studentName: item["StudentName"],
              imgSrc: defaultImg
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getAnswerPerson () {
      this.answerPersons = [];
      this.$gatewayHttp.screenQuestion.getAnswerPerson({
        ssID: this.screenQuestion.id
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.answerPersons.push({
                imgSrc: defaultImg,
                studentID: item["StudentID"],
                studentName: item["StudentName"],
                answer: item["SAContent"],
                isTrue: item["IsCorrect"],
                comment: item["SAComment"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    showOptionPersons (item) {
      if (item.total > 0) {
        this.optionPersonsSelected = item;
        this.optionPersonsShow = true;
        this.getOptionPerson();
      }
    },
    getSurveyPerson () {
      this.$gatewayHttp.screenQuestion.getSurveyPerson({
        ssID: this.screenQuestion.id
      }).then(res => {
        if (res.Result) {
          this.surveyInfo = {
            yes: res["YesMaster"],
            no: res["NoMaster"]
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    toSurvey () {
      Dialog.confirm({
        title: '提示',
        message: '是否下发调研？'
      }).then(() => {
        this.$gatewayHttp.screenQuestion.postSetSurvey({
          ssID: this.screenQuestion.id
        }).then(res => {
          if (res.Result) {
            this.$toast("下发调研成功");
            this.getDataInfo();
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("下发调研失败");
          }
        }).catch(() => {
          this.$toast("操作失败");
        });
      }).catch(() => {
      });
    },
    onConfirm () {
      // console.log(this.answer);
      // console.log(this.multiAnswer);
      if (!this.answer && this.multiAnswer.length === 0) {
        this.$toast(this.screenQuestionType === "4" ? "请先输入答案" : "请先选择答案");
      } else {
        this.realAnswer = this.answer || this.multiAnswer.sort().join('');
        this.$gatewayHttp.screenQuestion.postSetStopAnswer({
          ssID: this.screenQuestion.id,
          answer: this.realAnswer
        }).then(res => {
          if (res.Result) {
            this.selectAnswerShow = false;
            this.getDataInfo();
            this.$toast("结束答题成功");
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("结束答题失败");
          }
        }).catch(() => {
          this.$toast("操作失败");
        });
      }
    },
    toAnswer (sub) {
      if (this.screenQuestion.state === 1) return;
      this.answerPersonsShow = true;
      this.getAnswerInfo(sub);
    },
    getAnswerInfo (sub) {
      this.answerPersonsSelected = null;
      this.$gatewayHttp.screenQuestion.getAnswerInfo({
        ssID: this.screenQuestion.id,
        studentID: sub.studentID || ''
      }).then(res => {
        if (res.Result) {
          this.answerPersonsSelected = {
            createTime: formatDate(res.Data["SubmitTime"], "hh:mm:ss"),
            studentID: res.Data["StudentID"] + "",
            studentName: sub.studentName,
            id: res.Data["SAID"] + "",
            answer: res.Data["SAContent"] + "",
            comment: res.Data["SAComment"] + "",
            score: res.Data["SAScore"] || 0,
            master: res.Data["SAMaster"]
          };
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    toScore () {
      this.$gatewayHttp.screenQuestion.postSetComment({
        ssID: this.screenQuestion.id,
        saID: this.answerPersonsSelected.id,
        score: this.answerPersonsSelected.score,
        saComment: this.answerPersonsSelected.comment
      }).then(res => {
        if (res.Result) {
          this.answerPersonsShow = false;
          this.$toast("答题评分成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("答题评分失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  },
  beforeDestroy () {
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";

.course-screen-question-view {
  min-height: 100%;

  /deep/ .van-field__label {
    width: 70px;
  }

  .list-col {
    flex: 1;
    min-width: 20vw;
    text-align: center;
    padding: 10px 0;
  }

  .answer-content {
    width: 100%;
    word-break: break-all;

    /deep/ img {
      max-width: 100%;
    }
  }
}
</style>
